@import "../../../src/common/styles/sass/variables";
@import "../../../src/common/styles/sass/mixins";

.work {
    width: 450px;
    background-color: $secondColor;
    align-items: flex-start;
    margin-bottom: 20px;
    .workImage {
        display: flex;
        height: 300px;
        width: 100%;
        justify-content: center;
        align-items: center;
        ::before {
            content: '';
            position: absolute;
            background-color: #000000;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            opacity: 0.4;
            z-index: 2;
        }
        .workImg {
            height: 100%;
            width: 100%;
            position: relative;
            background-size: cover;
            background-position: center;

        }
        .butt {
            text-decoration: none;
            position: absolute;
            z-index: 4;
        }
    }
    .workDescription {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100px;
        width: 100%;

        flex-wrap:wrap;
        align-items: flex-start;
        position: relative;

        .descriptionTitle {
            width: 200px;
            height: 32px;

            line-height: 1.3;

            @include text($firstFont, 24px, 600, #000);
            //font-family: $firstFont;
            //font-size: 24px;
            //font-weight: 600;
            //color: #000;

            margin-top: 13px;
            margin-left: 20px;
            position: relative;
            ::before     {
                content: '';
                position: absolute;
                width: 50px;
                height: 1.3px;
                background: $secBgColor;
                bottom: -2px;
                right: 85%;
            }
        }

        .description {
            display: inline-block;
            @include text($firstFont, 16px, 700, $firstColor);
            //font-family: $firstFont;
            //font-size: 16px;
            //font-weight: 700;
            //color: $firstColor;


            text-transform: uppercase;
            margin-bottom: 15px;
            margin-left: 20px;
            word-spacing: 9px;
        }
    }
}










